

































/**
 *
 * @author fqq
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";

import Url from "../../../api";

@Component({
  components: {}
})
export default class AddOrgProp extends Vue {
  @Prop({ type: String, default: "添加组织机构类型" })
  dialogTitle?: string;

  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;

  @Prop({ type: Object, default: () => ({}) })
  data!: any;

  hasCustomRole: boolean = qTouch.getBaseTypeByCode("organization");

  @Watch("dialogVisible")
  handleVisible(newValue: boolean) {
    this.addDialog = newValue;
    if (newValue) {
      this.$nextTick(() => {
        let ref: any = this.$refs.addForm;
        ref.resetFields();
        this.addForm = JSON.parse(JSON.stringify(this.data));
        this.getTypeAndAttrs();
      });
    }
  }

  addDialog = false;
  addForm: any = {
    id: "",
    name: "",
    code: ""
  };
  loading = false;
  formRules: any = {
    name: { required: true, trigger: "blur", message: "请输入名称" },
    code: { required: true, trigger: "blur", message: "请输入编码" }
  };
  transferData: any = {
    data: [],
    selected: []
  };

  closeDialog() {
    this.addDialog = false;
    this.$emit("close");
  }

  saveSensorAttribute() {
    let rest: any = this.$refs.addForm;
    rest.validate((valid: any) => {
      if (valid) {
        this.loading = true;
        let url = this.addForm.id ? Url.getTypeDetail + "/updateTypeAndAttrs/organization" : Url.getTypeDetail + "/addTypeAndAttrs/organization";
        let typeVO = Object.assign(this.addForm, { attrs: this.transferData.selected });
        this.$http
          .post(url, typeVO)
          .then((res: any) => {
            this.loading = false;
            if (res.data.status) {
              this.$message.success("操作成功");
              this.addDialog = false;
              this.$emit("submit", res.data.data);
            } else {
              this.$message.error(res.data.message);
            }
          })
          .catch(() => {
            this.loading = false;
          });
      } else {
        return false;
      }
    });
  }

  // 获取组织机构和自定义属性，用于transfer
  getTypeAndAttrs() {
    let _this = this;
    this.$http.get(Url.getTypeDetail + "/attr/transferData/organization?typeDetailId=" + this.addForm.id).then((res: any) => {
      if (res.status) {
        if (res.data.status) {
          let result = res.data.data;
          _this.transferData.data = [];
          result.data.forEach(function(item: any) {
            _this.transferData.data.push({
              key: item.id,
              label: item.attrName
            });
          });
          _this.transferData.selected = result.selected;
        } else {
          this.$message.error(res.data.message);
        }
      }
    });
  }
}
