
























































































import { Vue, Component, Watch, Prop } from "vue-property-decorator";
import AddOrgProp from "./components/AddOrgProp.vue";

import Url from "../../api";
import { formatterAttrType } from "@/utils/dataFormat";

@Component({
  // 过滤器
  filters: {
    formatterAttrType
  },
  components: {
    AddOrgProp
  }
})
export default class OrganizationCustomProps extends Vue {
  // 类型树信息
  treeMessage: any = {
    // 过滤条件
    filterText: "",
    // tree数据
    data: [],
    // 配置选项
    defaultProps: {
      children: "children",
      label: "name"
    }
  };
  // 选中表单信息
  basicForm: any = {
    name: ""
  };

  // 分页信息
  pagination: any = {
    condition: "",
    currentPage: 1,
    total: 0,
    tableData: [],
    pageSize: 10
  };

  // 添加或编辑树节点弹窗
  dialogTitle = "";
  visibleDialog: boolean = false;
  // 弹窗表单信息
  formData: any = {
    id: "",
    name: "",
    code: ""
  };
  mode: string = "add";

  hasCustomRole: boolean = qTouch.getBaseTypeByCode("organization");

  created() {
    this.getTypeTreeData();
  }

  // 根据父组件参数获取类型树数据
  getTypeTreeData() {
    this.$http.get(Url.getTypeDetail + "/findTypeTree/organization").then((res: any) => {
      if (res.status) {
        if (res.data.status) {
          let data = res.data.data;
          this.treeMessage.data = data ? data : [];
          if (!this.basicForm.id) {
            this.handleNodeClick(this.treeMessage.data[0]);
          }
        } else {
          this.$message.error(res.data.message);
        }
      }
    });
  }

  // 树节点点击事件
  handleNodeClick(data: any) {
    this.basicForm = Object.assign({}, data);
  }

  // 监听是否tree过滤条件
  @Watch("treeMessage.filterText")
  setFilterText(val: any) {
    (this.$refs.tree as any).filter(val);
  }

  // 过滤tree
  filterNode(value: any, data: any) {
    if (!value) return true;
    return data[this.treeMessage.defaultProps.label].indexOf(value) !== -1;
  }

  // 打开节点弹窗
  openTypeTreeNodeForm(buttonName: any, data: any) {
    switch (buttonName) {
      case "add":
        this.dialogTitle = "添加组织机构类型";
        this.formData = {
          parentId: data.id,
          treeLevel: data.treeLevel + 1
        };
        break;
      case "edit":
        this.dialogTitle = "编辑组织机构类型";
        this.formData = Object.assign({}, data);
        break;
      case "addTop":
        this.dialogTitle = "添加组织机构类型";
        this.formData = { treeLevel: 1 };
        break;
    }
    this.mode = buttonName;
    this.visibleDialog = true;
  }

  // 删除节点
  remove(node: any, data: any) {
    this.$confirm("删除该类型将会删除其所有子类型及下属数据，是否删除？", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(() => {
      this.$http.get(Url.deleteType + "/" + data.id).then((res: any) => {
        if (res.status) {
          if (res.data.status) {
            this.$message.success("操作成功");
            if (data.id === this.basicForm.id) {
              this.basicForm.id = null;
            }
            this.getTypeTreeData();
          } else {
            this.$message({
              showClose: true,
              message: res.data.message,
              type: "error"
            });
          }
        }
      });
    });
  }

  // 保存树节点
  saveTypeAndAttr(data: any) {
    this.visibleDialog = false;
    this.basicForm = Object.assign({}, data);
    this.getTypeTreeData();
    if (this.mode === "edit") {
      // this.getCustomAttributeList();
      (this.$refs["baseDataInfoTable"] as any).getCustomAttributeList();
    }
  }

  closeDialog() {
    this.visibleDialog = false;
  }
}
